/* eslint no-param-reassign: ["error", { "props": false }] */

import isValidEmail from 'lib-bb-email';

export default function uiBbEmail() {
  /**
   * @name uiBbEmail
   * @type {object}
   * @description
   * Custom form validation for email input field
   *
   * @example
   * <form name="form">
   *   <label for="emailInputId">Email</label>
   *   <input type="text"
   *     id="emailInputId"
   *     name="emailInput"
   *     data-ng-model="email"
   *     ui-bb-email-ng />
   *     <span data-ng-if="form.emailInput.$error.uiBbEmail">
   *     Please input a valid Email
   *   </span>
   * </form>
   */
  return {
    require: 'ngModel',
    link: (scope, elm, attrs, ctrl) => {
      ctrl.$validators.uiBbEmail = (modelValue) => {
        if (ctrl.$isEmpty(modelValue)) {
          return true;
        }

        return isValidEmail(modelValue);
      };
    },
  };
}



